/**
 * Layout component that queries for data
 * with Gatsby's useStaticQuery component
 *
 * See: https://www.gatsbyjs.com/docs/use-static-query/
 */

import * as React from "react";
import { useEffect } from "react";
import "../../node_modules/bootstrap/dist/css/bootstrap.min.css";
import "/node_modules/react-toastify/dist/ReactToastify.css";
import "../styles/assets/css/general.css";
import "../styles/assets/css/custom.css";
import "../styles/assets/css/style.css";
import "../styles/assets/css/queries.css";

import "../../node_modules/swiper/swiper-bundle.css";
import "../../node_modules/swiper/swiper.css";
import AOS from "aos";
import "aos/dist/aos.css";

import PropTypes from "prop-types";
import Header from "./common/header";
import Footer from "./common/footer";
import { ToastContainer } from "react-toastify";
import { Chat, bottomAnimation, rightAnimation } from "../core/const";
// import Progress from "./common/scroll-progress";
import SocialLinks from "./common/social-links";
import { graphql } from "gatsby";

const Layout = (props) => {
  const { children, db } = props;
  React.useEffect(() => {
    rightAnimation();
    bottomAnimation();
    Chat();
  }, []);

  useEffect(() => {
    AOS.init();
  }, []);

  return (
    <React.Fragment>
      <Header db={db} />
      {/* <Progress /> */}
      <SocialLinks />
      {children}
      <ToastContainer
        position="bottom-center"
        autoClose={5000}
        limit={1}
        hideProgressBar={false}
        newestOnTop={true}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
      <Footer db={db} />
    </React.Fragment>
  );
};

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default Layout;

export const queryData = graphql`
  query MyQuery {
    site {
      siteMetadata {
        firestore {
          apiKey
          appId
          authDomain
          messagingSenderId
          projectId
          storageBucket
        }
      }
    }
  }
`;
